import * as React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import BackgroundImage from 'gatsby-background-image'

import Layout from "../components/layout"
import FullScreenSection from "../components/full-screen-section"
import Seo from "../components/seo"

import styled from 'styled-components'

const IndexPage = () => {

  const data = useStaticQuery(
    graphql`
      query {
        bg1:

        file(relativePath: { eq: "bg-ae1.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

        bg2:
        file(relativePath: { eq: "bg-ae2.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

      }
    `
  )

  // const data2 = useStaticQuery(
  //   graphql`
  //     query {
  //       desktop: file(relativePath: { eq: "obj.jpg" }) {
  //         childImageSharp {
  //           fluid(quality: 90, maxWidth: 1920) {
  //             ...GatsbyImageSharpFluid_noBase64
  //           }
  //         }
  //       }
  //     }
  //   `
  // )  

  const bg1 = data.bg1.childImageSharp.fluid
  const bg2 = data.bg2.childImageSharp.fluid

  return (
    <Layout>
      <Seo title="Actividades educativas" />

      <FullScreenSection>

        <BackgroundImage
          Tag="section"
          className={'container-fluid fullheight-ribbon g-0 d-flex flex-column'}
          fluid={bg1}
          backgroundColor={`#040e18`}
        >

          <div className={`row d-flex flex-column `}>
            <p className={`text-white headline pb-0 pointer-cursor`} onClick={() => { window.scrollTo(0, 1000) }}><span className={`bg-black`}>Actividades educativas /</span><span className={`fs-5 bg-black d-none d-md-inline-block d-lg-inline-block d-xl-inline-block`}>Actividades realizadas por parte de nuestro equipo de trabajo y que son gratuitas para la comunidad.</span></p>
          </div>

          <div className={`row d-flex flex-1`}>

            <div className={`col pt-1 d-flex flex-column`}>

              {/*<div className={`col-lg-6 col-xl-6 col`}>

                <p className={`text-white text-end bg-black mt-5 pb-0 mb-0 pointer-cursor headline`} onClick={() => { window.scrollTo(0, 1000) }}>Actividades educativas /</p><p className={`text-black text-end pt-0 mt-0`}>Actividades realizadas por parte de nuestro equipo de trabajo y que son gratuitas para la comunidad</p>
                

              </div>*/}

              <div className={`col-lg-8 col-xl-8 col d-flex flex-1 flex-column flex-items-middle space-between`}>

                <div className={`col-6 offset-lg-4 offset-xl-4 pointer-cursor`} onClick={() => { window.scrollTo(0, 1000) }}><p className={`text-right mb-0 pb-0 bg-gray`}><span className={`text-white text-end pb-0 mb-0 me-1 fs-1`}>Clase-Taller <i className={`fs-3 fa fa-chevron-down pe-2`} aria-hidden="true"></i></span></p><span className={`text-black`}>1. Arte integrado a la arquitectura y el Taller de Diseño Integrado</span></div>
                <div className={`col-5 offset-lg-3 offset-xl-3 pointer-cursor`} onClick={() => { window.scrollTo(0, 1000) }}><p className={`text-right mb-0 pb-0 bg-gray`}><span className={`text-white text-end pb-0 mb-0 me-2 fs-1`}>Mesa redonda <i className={`fs-3 fa fa-chevron-down pe-2`} aria-hidden="true"></i></span></p><span className={`text-black`}>2. Resignificación del concepto de patrimonio y del espacio público. La situación del mural hoy.</span></div>
                <div className={`col-7 offset-lg-5 offset-xl-5 pb-5 pointer-cursor`} onClick={() => { window.scrollTo(0, 1000) }}><p className={`text-right mb-0 pb-0 bg-gray`}><span className={`text-white text-end pb-0 mb-0 ms-5 fs-1 mb-5`}>Workshop <i className={`fs-3 fa fa-chevron-down pe-2`} aria-hidden="true"></i></span></p><span className={`text-black`}>3. Digitalizando la ciudad. Arte, tecnología y educación.</span></div>

              </div>
            </div>                        

          </div>

          <div className={`row ribbon no-gutters`}>

            <div className={`col-3 ribbon-blue`}></div>
            <div className={`col-2 ribbon-red`}></div>
            <div className={`col-4 ribbon-navy`}></div>
            <div className={`col-3 ribbon-gray`}></div>

          </div>

        </BackgroundImage>

      </FullScreenSection>

      <FullScreenSection>

        <BackgroundImage
          Tag="section"
          className={'container-fluid fullheight-ribbon d-flex flex-column'}
          fluid={bg2}
          backgroundColor={`#040e18`}
        >

          <div className={`row d-flex flex-1`}>

            <div className={`col-lg-5 col-xl-5 offset-lg-1 offset-xl-1 col d-flex flex-column`}>

              <div className={`pt-5`}>

                <p className={`text-black text-start ps-1 fs-5 mb-0 pb-0`}><i><b>1.- Arte integrado a la arquitectura y el Taller de Diseño Integrado</b></i></p>
                <p className={`text-black text-start ps-1 fs-6 mt-0 pt-0 pb-0 mb-0`}>Charla Taller - Junio 2022</p>
                <p className={`text-black text-start ps-1 fs-6 mt-0 pt-0`}>Mónica y Stella Salinero.</p>
                <p className={`text-black text-start ps-1 fs-5 mb-0 pb-0`}><i><b>2.- Resignificación del concepto de patrimonio y del espacio público.</b></i></p>
                <p className={`text-black text-start ps-1 fs-6 mt-0 pt-0 pb-0 mb-0`}>Mesa redonda - Agosto 2022</p>
                <p className={`text-black text-start ps-1 fs-6 mt-0 pt-0`}>Eduardo Martínez Bonati, Pelagia Rodriguez, Angela Benavente, Gastón Vega e invitados.</p>
              
              </div>

              </div>

              <div className={`col-lg-6 col-xl-6 col d-flex`}>

                <div className={`d-flex flex-1 flex-column items-end pe-5 ps-0`}>
{/*                  <div className={`pb-5`}>
                    <p className={`text-end lh-sm`}>Equipo de investigación:</p>
                    <p className={`text-end  lh-sm`}>David Maulén y Vania Montgomery</p>

                    <p className={`lh-sm text-end`}>Invitados Clase taller: Soledad Novoa,Francisco Brugnoli, Peter Himmel, Cristobal Molina, Ignacio Szmulewicz</p>
                    <p className={`lh-sm text-end`}>Invitados Mesa Redonda: Gaston Vega, Rodrigo Gertosio,  Pablo Altikes</p>
                    <p className={`lh-sm text-end`}>Workshop de Fotogrametría a cargo de Kurt Mallonek</p>
                    <p className={`lh-sm text-end`}>Agradecimientos especiales al CNAC Gastón Vega, Angela Quintana, Pelagia Rodriguez,</p>
                  </div>*/}

                <p className={`text-black text-right ps-1 fs-5 mb-0 pb-0`}><i><b>3.- Digitalizando la ciudad. <br />Arte, tecnología y educación</b></i></p>
                <p className={`text-black text-right ps-1 fs-6 mt-0 pt-0 pb-0 mb-0`}>Workshop de Fotogrametría - Agosto 2022</p>                  
                <p className={`text-black text-right ps-1 fs-6 mt-0 pt-0`}>Kurt Malonnek.</p>                  

                </div>

              </div>
                                    

          </div>    
            
            <div className={`row ribbon no-gutters`}>

              <div className={`col-3 ribbon-blue`}></div>
              <div className={`col-2 ribbon-red`}></div>
              <div className={`col-4 ribbon-navy`}></div>
              <div className={`col-3 ribbon-gray`}></div>

            </div>

        </BackgroundImage>

      </FullScreenSection>

    </Layout>
  )

}

const StyledBackgroundSection = styled(IndexPage)`
  width: 100%;
  background-position: bottom center;
  background-repeat: repeat-y;
  background-size: cover;
`

export default IndexPage
